import { Apps } from '../imports/api/apps.js';


// ###  Create admin user at first start  ###


if (Meteor.users.find().count() === 0) {
	
	// Create the admin role
	Roles.createRole('admin', {unlessExists: true});

	var adminPassword = Meteor.settings.adminPassword;

	var users = [
		{username:"admin",roles:['admin']},
	];

	_.each(users, function (user) {
		var id;
		id = Accounts.createUser({
			username: user.username,
			email: "Admin",
			password: adminPassword,
			profile:{name:"Admin"}
		});

		if (user.roles.length > 0) {
			Roles.addUsersToRoles(id, user.roles);
		}
	});
}


if (Apps.find().count() === 0) {

	var defaultApps = [
		{name:"Live", customApp:false, onlyTeacher:false, order:1, doc_user:false, doc_admin:false, last_version:"1.3.3", url:"http://live.beekee.box", icon:"beekee-live.png", description:"Beekee Live promote real-time interaction by allowing learners to express themselves asking questions, posting photos or sharing files.", installed:true, version: "1.4", hidden:false},
		{name:"Resources", customApp:false, onlyTeacher:false, order:2, doc_user:false, doc_admin:false, last_version:"1.3.3", url:"http://resources.beekee.box", icon:"beekee-resources.png", description:"With Beekee Resources, you can easily share files with your learners.", installed:true, version: "0.1", hidden:false},
		{name:"Wheel", customApp:false, onlyTeacher:true, order:3, doc_user:false, doc_admin:false, last_version:"0.7", url:"http://wheel.beekee.box", icon:"beekee-wheel.png", description:"Beekee Wheel is a simple random picker wheel that allow you to pick up a random name.", installed:true, version: "0.8", hidden:false},
		{name:"Timer", customApp:false, onlyTeacher:false, order:2, doc_user:false, doc_admin:false, last_version:"1.3.3", url:"http://timer.beekee.box", icon:"beekee-timer.png", description:"Beekee Timer is a simple timer that lets your learners know how much time they have left.", installed:true, version: "0.1", hidden:false},
		{name:"Moodle", customApp:true, onlyTeacher:false, order:4, doc_user:"moodle_teacherdoc.pdf", doc_admin:false, last_version:"xx", url:"http://moodle.beekee.box", icon:"moodle.png", description:"Moodle is a free, online Learning Management system enabling educators to create their own private website filled with dynamic courses that extend learning, any time, anywhere.", installed:true, version: "3.8", hidden:false},
	];

	_.each(defaultApps, function (defaultApps) {
		Apps.insert(defaultApps);
	});
}